.sec_heading {
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  margin-bottom: 0;
}

.title {
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  margin-top: 30px;
  margin-bottom: 5px;
}

.sub_title {
  font-weight: 300;
  font-size: 16px;
  line-height: 18px;
}

ul {
  margin-left: 45px;
}

.exp_eg_div {
  display: inline-block;
  width: 23%;
  margin: 1%;
  color: #333333;
  height: 190px;
  border-radius: 10pz;
  border-radius: 8px;
  vertical-align: top;
}

.exp_eg_div img {
  width: 100%;
  height: 175px;
  object-fit: contain;
  border-radius: 8px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
}

.exp_eg_div p {
  margin-top: 15px;
  text-align: center;
  font-weight: 500;
  font-size: 14px;
}

.exp_eg_div:hover img {
  object-fit: cover;
}

.exp_eg_div:hover p {
  font-weight: 600;
}
