.dark_theme {
  background: #333333;
  color: #fff;
  padding: 50px 15px;
}

.light_theme {
  background: #f8f8f8;
  color: #333333;
  padding: 50px 15px;
}

.timeline-table tr td:nth-child(1) {
  width: 100px;
}

.timeline-table tr td {
  padding-top: 30px;
  vertical-align: top;
}

.timeline-table .qual {
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 5px;
}

.timeline-table .institute {
  font-weight: 300;
  font-size: 16px;
  line-height: 18px;
}

.about_me .profile_pic {
  display: block;
  margin: auto;
}

.about_me .name {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}

.contact-div {
  display: inline-block;
  width: 25%;
  text-align: center;
  vertical-align: top;
  padding: 15px;
}

.contact-div i {
  display: block;
  width: 40px;
  height: 40px;
  background: rgb(9, 189, 189);
  border-radius: 50%;
  padding: 12px 8px;
  margin: auto;
  margin-bottom: 15px;
}

.contact-div p,
.contact-div a {
  margin-bottom: 0;
  text-align: center;
  color: #fff;
}

@media (max-width: 575px) {
  .contact-div {
    width: 100%;
  }
}

@media (min-width: 576px) and (max-width: 992px) {
  .contact-div {
    width: 50%;
  }
}
